require("core-js/modules/es.array.filter");

require("core-js/modules/es.array.map");

require("core-js/modules/es.array.slice");

require("core-js/modules/es.object.to-string");

require("core-js/modules/es.promise");

function DoAxes() {
  var tmlbl = []; //[{"id" : 1 ,tm: "06:00", tmx:3.001}],

  var axislbl = [];

  var test = function test(arg) {
    return new Promise(function (resolve, reject) {
      var path = "return:";

      try {
        var x = 100 / arg;

        if (isNaN(arg) || !isFinite(x)) {
          console.log(isNaN(arg), isFinite(x));
          throw "Parameter is not a number!" + x;
        }

        resolve(path + arg + x);
      } catch (e) {
        reject(e);
      }
    });
  };

  var fillTimeLabel = function fillTimeLabel(width, offset, minutes) {
    //tmlbl:[{"id" : 1 ,tm: "06:00", tmx:3.001}]
    while (tmlbl.length > 0) {
      tmlbl.pop();
    }

    var step = offset;
    var i = 1,
        pxInTicks = parseFloat((width - offset * 2) / minutes),
        tm = new Date(),
        h1 = 6 - tm.getTimezoneOffset() / 60;
    tm.setHours(h1, 0, 0);

    while (i <= 1440) {
      step += pxInTicks;
      var row = {
        id: Math.round(i),
        tm: new Date(tm).toISOString().slice(11, 16),
        tmx: step
      }; //console.log(row);

      tmlbl.push(row);
      tm = new Date(tm).getTime() + 60 * 1 * 1000;
      i++;
    }

    return tmlbl;
  };

  var fillAxisLabel = function fillAxisLabel(offset, minutes, tickNum) {
    //tmlbl:[ { id: 1, x: 3.0652777777777778, tml: '06:00' }],
    if (tmlbl.length < 2) return [{
      id: -1,
      x: "Please call fillTimeLabel function first!"
    }];
    var id = 1,
        step = oddTickSize(minutes, tickNum),
        n = 1;
    axislbl = tmlbl.map(function (a) {
      if (a.id == id) {
        var row = {
          id: a.id,
          x: a.tmx,
          tml: a.tm,
          n: n
        }; //console.log(id,step)

        id += step;
        n++;
        return row;
      }
    }).filter(Boolean);
    var a = tmlbl.slice(-1)[0];
    var endTick = {
      id: a.id,
      x: a.tmx,
      tml: a.tm,
      n: n
    };
    axislbl.push(endTick); //console.log(axislbl);

    return axislbl;
  };

  var oddTickSize = function oddTickSize(minutes, mayBeSize) {
    var rtn = minutes / 24;

    for (var i = mayBeSize; i <= 24; i++) {
      var remaider = minutes % i;

      if (remaider == 0) {
        rtn = minutes / i;
        break;
      } //console.log(mayBeSize, " ###oddTickSize ", rtn);

    } // console.log(mayBeSize, " ###oddTickSize ", rtn);


    return rtn;
  }; // Exhibating private methods


  this.test = function (arg) {
    return test(arg);
  };

  this.fillTimeLabel = function (width, offset, minutes) {
    return fillTimeLabel(width, offset, minutes);
  };

  this.fillAxisLabel = function (offset, minutes, tickNum) {
    return fillAxisLabel(offset, minutes, tickNum);
  };
}

module.exports = DoAxes;